const fontSelect = document.getElementById("font-select")
const head = document.getElementsByTagName("head")[0]
const body = document.getElementsByTagName("body")[0]
let allFonts = []

const node = document.createElement("link")
node.rel = "stylesheet"

const addFont = font => {
    node.href = "https://fonts.googleapis.com/css2?family=" + font + ":wght@400;500;700&display=swap"
}

const removeFont = () => {
    // document.getElementsByTagName("body")[0].classList.remove(fontSelect[i].value)
    body.classList.remove("roboto","inter","dm-sans","noto-serif","noto-sans")
    for(let i=0;i<fontSelect.length;i++){
        if(fontSelect[i].value !== "") {
            allFonts.push(fontSelect[i].value.toLowerCase().replace(/ /g,"-"))
        }
    }
}

fontSelect.addEventListener("change", (event) => {
    const font = event.target.value.replace(/ /g,"+")
    if(font) {
        switch (font) {
            case "Roboto":
                removeFont()
                addFont(font)
                head.appendChild(node)
                document.getElementsByTagName("body")[0].classList.add("roboto")
                break;
            case "Inter":
                removeFont()
                addFont(font)
                head.appendChild(node)
                document.getElementsByTagName("body")[0].classList.add("inter")
                break;
            case "DM+Sans":
                removeFont()
                addFont(font)
                head.appendChild(node)
                document.getElementsByTagName("body")[0].classList.add("dm-sans")
                break;
            case "Noto+Serif":
                removeFont()
                addFont(font)
                head.appendChild(node)
                document.getElementsByTagName("body")[0].classList.add("noto-serif")
                break;
            case "Noto+Sans":
                removeFont()
                addFont(font)
                head.appendChild(node)
                document.getElementsByTagName("body")[0].classList.add("noto-sans")
                break;
            default:
                removeFont()
                break;
        }
    }
})